// index.js (Dashboard)
import { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import { supabase } from '../lib/supabaseClient';
import ProtectedRoute from '../components/ProtectedRoute';
import UploadForm from '../components/UploadForm';
import FileList from '../components/FileList';
import Link from 'next/link'; // <--- ADDED Link

export default function Dashboard() {
  const [files, setFiles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [user, setUser] = useState(null);
  const router = useRouter();

  const fetchUser = async () => {
    const { data } = await supabase.auth.getUser();
    setUser(data?.user ?? data);
  };

  async function fetchFiles() {
    try {
      setLoading(true)
      setError(null)

      const { data: { session } } = await supabase.auth.getSession()
      if (!session?.user) {
        setFiles([])
        return
      }

      const res = await supabase.storage.from('uploads').list(session.user.id)

      // DEBUG: log full response to diagnose undefined / duplicates
      console.debug('supabase.storage.list response:', res)

      if (res.error) throw res.error
      const sanitized = (res.data || [])
        .filter(Boolean)
        .filter(f => typeof f.name === 'string')
        .sort((a, b) => (b.updated_at || 0) - (a.updated_at || 0))

      setFiles(sanitized)
    } catch (err) {
      setError(err?.message || String(err))
    } finally {
      setLoading(false)
    }
  };

  useEffect(() => {
    fetchUser()
    fetchFiles()
  }, [])

  const handleLogout = async () => {
    await supabase.auth.signOut()
    router.push('/')
  }

  // const totalFiles = (files || []).length // Removed since profile data is moving

  return (
    <ProtectedRoute>
      <div className="dashboard">
        <header className="dashboard-header">
          <h1>Cloud Resource Manager</h1>
          <div className="dashboard-nav"> {/* ADDED className */}
            {/* ADDED Profile Link */}
            <Link href="/profile" className="nav-link">
              Profile
            </Link>
            <button className="btn ghost" onClick={handleLogout}>Logout</button>
          </div>
        </header>

        <div className="grid">
          {/* REMOVED: <aside className="profile-card"> block */}

          <main className="main-panel">
            <section className="upload-section card">
              <h2>Upload Files</h2>
              {/* Note: If UploadForm uses user or files data, you might need to pass it */}
              <UploadForm onUploadComplete={fetchFiles} />
            </section>

            <section className="files-section card">
              <h2>Your Documents</h2>
              {loading ? (
                <p className="muted">Loading...</p>
              ) : error ? (
                <p className="error">{error}</p>
              ) : (
                <FileList files={files} onDelete={() => fetchFiles()} onRefresh={fetchFiles} />
              )}
            </section>
          </main>
        </div>
      </div>
    </ProtectedRoute>
  )
}